
.GigCard{
    width: 300px;
    height: 360px;
    border-radius: 5px;
    border: 1px solid lightgray;
    margin-bottom: 25px;

    img{
        width: 100%;
        height: 50%;
        object-fit: cover;
    }
    .info{
        padding: 10px 20px;
        display: flex;
        flex-direction: column;
        gap:15px;

         .user{
            display: flex;
            align-items: center;
            gap:10px;

            img{
                width: 26px;
                height: 26px;
                border-radius: 50%;
            }
            span{
                color:black;
                text-decoration: none;
                
            }
        }
        
         p{
            color:black;
         }
         .star{
            display: flex;
            align-items: center;
            gap:5px;

            img{
                width: 14px;
                height: 14px;
            }
            span{
                font-size: 14px;
                color:#ffc108;
                
                .spandec{
                    text-decoration: none;
                }
             

            }
         }
    }
    hr{
        height: 0;
        border:0.5px solid lightgray
    }
    .details{
        padding: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        img{
            height: 16px;
            width: 16px;
            cursor:pointer;
        }
       .price{
        span{
            color:#999
        }
        h2{
        color:#555;
        font-size: 18px;
        font-weight: 400;
        }
       } 
    }
}