.catCards {
  
    color: white;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
    margin-left: 2rem;
    

   .container{
    width: 200px;
    height: 300px;
    display: flex;
    margin-right:15px;
    gap:20px
   }

  
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  
    .desc {
      font-weight: 300;
      position: absolute;
      top: 15px;
      left: 15px;
    }
  
    .title {
      position: absolute;
      top: 40px;
      left: 15px;
      font-size:24px;
      font-weight: 500;
    }
  }