.projectCard {
   border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid rgb(225, 225, 225);
  margin-left: 2rem;


  img {
    width: 100%;
    height: 70%;
    object-fit: cover;
  }

  .info {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 15px;

    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      object-fit: cover;
    }

    .texts {
      h2 {
        font-size: 14px;
        font-weight: 500;
      }

      span {
        font-size: 14px;
        font-weight: 300;
      }
    }
  }
}