.add {
    display: flex;
    justify-content: center;
  
    .container {
      width: 1024px;
      padding: 50px 0px;
  
      h1 {
        width: max-content;
        margin-bottom: 30px;
        color: gray;
        font-weight: 300;
      }
  
      .sections {
        display: flex;
        justify-content: space-between;
        gap: 100px;
  
        label {
          color: gray;
          font-size: 18px;
        }
  
        .info,
        .details {
          flex: 1;
          display: flex;
          flex-direction: column;
          gap: 10px;
          justify-content: space-between;
  
          input,
          select,
          textarea {
            padding: 20px;
          }
  
          .images{
            display: flex;
            align-items: center;
            gap: 20px;
  
            .imagesInputs{
              display: flex;
              flex-direction: column;
              gap: 20px;
            }
          }
  
          button {
            border: none;
            padding: 20px;
            color: white;
            font-weight: 500;
            font-size: 18px;
            background-color: #1dbf73;
            cursor: pointer;
          }
  
          .add{
            display: flex;
            justify-content: space-between;
  
            input{
              width: 80%;
            }
          }
  
          .addedFeatures{
            display: flex;
            gap: 20px;
  
            button{
              height: 30px;
              font-size: 12px;
              font-weight: 400;
              background: transparent;
              color: red;
              border: 1px solid red;
              display: flex;
              align-items: center;
              gap: 20px;
            }
          }
        }
      }
    }
  }